@import "../Abstract/__variables";
@import "../Abstract/__extend";
@import "../Abstract/__mixin";

.banner {
    &-video {
        position: fixed;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 0px;
        padding-bottom: 56.25%;
        z-index: -1;
        background-image: url('../images/home/img_banner.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        iframe {
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100%;
            border: none;
            .player {
                max-width: 100%;
                height: 100%; } } }
    &-buffer {
        padding-bottom: 56.25%;
 } }        // max-height: 100vh
.topic,.data,.work , .recruit, .people,.modal {
    background-color: white; }
.topic,.data,.recruit {
    position: relative;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &::after, &::before {
        content: '';
        position: absolute;
        right: 0px; }
    &::before {
        left: 0px;
        top: 80px;
        bottom: 0px;
        z-index: -2; }
    &::after {
        top: 0px;
        width: 100%;
        z-index: -1;
        bottom: 80px;
        transform: skewX(-22.5deg);
        transform-origin: bottom left; } }
.topic {
    padding-top: 80px;
    min-height: 680px;
    flex-direction: column;
    &::before {
        background-color: #d2dddd; }
    &::after {
        max-width: calc(50% - 104px);
        background-color: #89b2c1; }
    &-new {
        max-width: 1120px;
        width: 100%;
        position: absolute;
        top: 0px;
        z-index: -2;
        left: 50%;
        transform: translateX(-50%);
        height: 80px; }
    &-new,&-dl {
        display: flex;
        justify-content: flex-start;
        align-items: center; }
    .text-new {
        color: $colorDefault;
        @extend %_f22R;
        margin-right: 50px; }
    &-dt {
        margin-right: 34px; }
    &-title {
        p {
            max-width: 639px;
            margin: 0px auto;
            @extend %_f20W;
            text-align: center;
            span {
                display: inline-block; } } }
    &-h2 {
        text-align: center;
        @extend %_f80R; }
    &-buffer {
        display: block;
        min-width: 400px;
        width: 25%;
        // min-height: 200px
        span {
            display: block;
            width: 100%;
            padding-bottom: 50%; } } }
.data {
    height: 620px;
    color: white;
    padding-top: 80px;
    &::before {
        background-color: #89b2c1; }
    &::after {
        max-width: calc(50% + 170px);
        background-color: #484f72; }
    img {
        display: table;
        margin: 0px auto; }
    &-h2 {
        @extend %_f40W;
        text-align: center;
        margin: 25px 0px 8px; }
    p {
        @extend %_f20W;
        max-width: 830px;
        width: 100%;
        margin: 0px auto; }
    .more {
        display: block;
        line-height: 50px;
        width: 100px;
        margin: 60px auto 0px;
        border-top: 2px solid white;
        @extend %_f14R;
        text-align: center; } }
.aboutUs {
    background-color: white;
    padding: 143px 0px;
    .container {
        position: relative; } }
.aboutUs {
    img {
        display: block; }
    &-title {
        position: absolute;
        z-index: 1;
        right: 0px;
        top: 0px;
        max-width: 435px;
        width: 100%;
        p {
            @extend %_f20W;
            margin-top: 12px; } }
    &-h2 {
        &>span {
            display: block;
            text-align: right; }
        .text-en {
            @extend %_f80R; }
        .text-nihon {
            @extend %_f40W; } }
    &-figure {
        width: 100%;
        position: relative;
        z-index: 0;
        p {
            position: absolute;
            bottom: 0px;
            height: 0px;
            max-width: calc(50% - 435px);
            width: 100%;
            min-width: 0px;
            z-index: 1;
            right: 0px; }
        .shapeSkew {
            left: 0px;
            transform: translate(-100%, 50%); } } }
.people, .aboutUs {
    .shapeSkew {
        position: absolute;
        line-height: 6rem;
        bottom: 0px;
        width: 400px;
        @extend %_f20W; } }
.people {
    padding: 150px 0px 85px;
    background-image: url('../images/home/img_partent.jpg');
    &-figure {
        position: relative;
        z-index: 0;
        .shapeSkew {
            transform: translateY(50%);
            right: 40px; } }
    &-top {
        position: relative;
        display: flex;
        justify-content: flex-end; }
    &-title {
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0px;
        max-width: 412px;
        width: 100%;
        p {
            @extend %_f20W;
            margin-top: 12px; } }
    &-h2 {
        &>span {
            display: block;
 }            // text-align: right
        .text-en {
            @extend %_f80R; }
        .text-nihon {
            @extend %_f40W; } }
    @include commonPeople; }
.work {
    padding: 120px 0px 80px;
    &-h2 {
        span {
            display: block;
            text-align: center; }
        .text-en {
            @extend %_f80R; }
        .text-nihon {
            @extend %_f40W;
            line-height: 1; } }
    &-img {
        display: flex;
        justify-content: space-between;
        margin-top: 62px; }
    &-figure {
        max-width: calc(50% - 15px);
        width: 100%;
        img {
            display: block; }
        p {
            line-height: 6rem;
            @extend %_f20W;
            padding: 0px 20px; } } }
.recruit {
    height: 680px;
    padding-top: 80px;
    display: flex;
    align-items: center;
    &::before {
        background-color: #d2dddd; }
    &::after {
        max-width: calc(50% + 330px);
        background-color: #89b2c1;
        bottom: 378px; }
    &-wrap {
        background-color: white;
        height: 400px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .shapeSkew {
            line-height: 7rem;
            width: 405px; } }
    &-h2 {
        @extend %_f80R;
        line-height: 1;
        margin-bottom: 16px; } }
.modal {
    padding-bottom: 215px;
    position: relative;
    z-index: 1;
    .arrow {
        position: absolute;
        max-width: calc(50% - 230px);
        z-index: 5;
        width: 100%;
        height: 0px;
        min-width: 0px;
        top: 0px;
        cursor: pointer;
        &.right {
            left: 0px;
            .arrow-wrap {
                right: 0px;
                transform: translate(100%, -50%);
                background-image: linear-gradient(to right, $colorDefault 50%, rgba($colorDefault, 0.8) 50%);
                background-position: 0% 50%;
                img {
                    transform: rotate(180deg); } }
            &:hover {
                .arrow-wrap {
                    background-position: 100% 50%; } } }
        &.left {
            right: 0px;
            .arrow-wrap {
                left: 0px;
                transform: translate(-100%, -50%);
                background-position: 100% 50%;
                background-image: linear-gradient(to left, $colorDefault 50%, rgba($colorDefault, 0.8) 50%); }
            &:hover {
                .arrow-wrap {
                    background-position: 0% 50%; } } }
        &-wrap {
            position: absolute;
            top: 0px;
            height: 60px;
            width: 60px;
            background-size: 200%;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease; } }
    &-slider {
        width: 25%;
        min-width: 400px;
        margin: 0px auto;
        position: absolute;
        top: 0px;
        left: 50%;
        z-index: 1;
        transform: translate(-50%, -50%); }
    &-show {
        padding-bottom: 100%;
        position: relative;
        .slick-list {
            // width: calc(100% + 20px)
            left: 50%;
            transform: translateX(-50%);
            width: calc(100% * 3 + 60px);
            position: absolute;
            z-index: 1;
            top: 0px;
            // left: 0px
            height: 100%;
            overflow: visible !important; }
        .slick-track {
            height: 100%; } }
    &-figure {
        height: 100%;
        margin: 0px 10px;
        position: relative;
        z-index: 0;
        a {
            position: absolute;
            top: 0px;
            left: 0px;
            display: block;
            width: 100%;
            height: 100%;
            z-index: 1; }
        &:focus {
            outline: none; }
        &.active {
            &::after {
                opacity: 0;
                visibility: hidden; }
            dl {
                .img_num {
                    opacity: 0.6; } } }
        &::after {
            @include psuedoDefault(0px);
            background-color: #eeeeee;
            z-index: 2;
            transition: all 0.3s ease;
            opacity: 0.5;
            visibility: visible; }
        .img_bg {
            height: 50%;
            display: block;
            background-size: cover;
            background-position: center center; }
        @for $var from 1 through 11 {
            &.v#{$var} {
                .img_bg {
                    background-image: url('../images/home/img_modal#{$var}.jpg'); } } }
        dl {
            height: 50%;
            // background-color: #eeeeee
            padding: 0px 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            border-bottom: 1px solid #dddddd;
            border-right: 1px solid #dddddd;
            border-left: 1px solid #dddddd;
            .img_num {
                @extend %_f25R;
                line-height: 1; }
            span {
                display: block;
                text-align: center; }
            .text-over {
                @extend %_f24W;
                line-height: 1.4;
                display: flex;
                height: 80px;
                align-items: center;
                justify-content: center; }
            .text-under {
                @extend %_f14R;
                color: $colorDefault;
                line-height: 1;
                padding-top: 20px;
                width: 100px;
                text-align: center;
                border-top: 1px solid #333333;
                margin: 0px auto; } } }
    &-content {
        position: fixed;
        z-index: 20;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        visibility: hidden;
        transition: all 0.3s ease;
        &.active {
            visibility: visible;
            &::after {
                opacity: 1; } }
        &::after {
            @include psuedoDefault(0px);
            z-index: -1;
            background-color: rgba(black, 0.8);
            opacity: 0;
            transition: all 0.3s ease; } }
    &-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: 1000px;
        width: 100%;
        transform: translate(-50%, -50%);
        background-color: white;
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease;
        padding: 70px 20px;
        text-align: center;
        &.active {
            visibility: visible;
            opacity: 1; }
        span {
            display: block; }
        .text-num {
            @extend %_f25R;
            line-height: 1;
            opacity: 0.6; }
        .text-title {
            @extend %_f24W; }
        dl {
            max-width: 800px;
            margin: 0px auto;
            margin-top: 35px; }
        dd {
            margin-top: 70px;
            text-align: left; }
        .cross-button {
            position: absolute;
            top: 0px;
            right: 0px;
            height: 60px;
            width: 60px;
            display: block;
            background-color: $colorDefault;
            cursor: pointer;
            &::after,&::before {
                content: '';
                top: calc(50% - 1px);
                position: absolute;
                left: 50%;
                width: 70%;
                height: 2px;
                background-color: white;
                z-index: 1; }
            &::after {
                transform: translateX(-50%) rotate(45deg); }
            &::before {
                transform: translateX(-50%) rotate(-45deg); } } } }
@import "../Layout/__common";
